.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* border: 1px solid gray; */
    background-color: whitesmoke;
    padding: 0.2rem;
}
.nav-items{
    width: 50%;   
}
.nav-items>ul{
    display: flex;
    gap: 3rem;
    list-style: none;
    justify-content: flex-end;  
    padding-right: 1rem;
    align-items: center;
} 
.nav-items>ul>li{
    font-size: medium;
    font-weight: 600;
    cursor: pointer;   
} 
.nav-items>ul>li:hover{
    color: gray;

}

.title{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    gap: 15px;
}
.title >a> img{
    width: 60px;
    aspect-ratio: 1;
    object-fit: cover;
    border-radius: 50%;
    cursor: pointer;
}